footer {
  align-items: center;
  background-color: #212121;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  display: flex;
  height: 70px;
  justify-content: center;
}

footer p {
  color: rgba(255, 255, 255, 0.7);
}

@media (max-width: 480px) {
  footer {
    height: 40px;
  }

  footer p {
    font-size: 14px;
  }
}
.about {
  background-color: #212121;
  border-radius: 15px;
  padding: 30px 28px;
  margin-bottom: 84px;
}

.intro {
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  display: flex;
  margin-bottom: 30px;
  padding-bottom: 30px;
}

.avatar {
  background-image: url(/me/static/media/avatar.d14730b3.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  height: 120px;
  margin-right: 30px;
  width: 120px;
}

.name {
  flex-grow: 1;
}

.name p {
  font-size: 24px;
}

.name p + p {
  color: rgba(255, 255, 255, 0.7);
  margin-top: 12px;
}

.contact {
  background-color: #383838;
  border-radius: 15px;
  height: 120px;
  padding: 10px;
  width: 68px;
}

.contact .image {
  align-items: center;
  display: flex;
  height: 48px;
  justify-content: center;
  width: 48px;
}

.contact .divider {
  background-color: rgba(255, 255, 255, 0.15);
  display: block;
  height: 1px;
  margin: 4px 0;
  width: 100%;
}

.description p {
  line-height: 28px;
}

.description p + p {
  margin-top: 16px;
}

/*
  Mobile styles
*/

@media (max-width: 640px) {
  .about {
    max-width: 480px;
    margin: 0 auto 42px;
  }
}

@media (max-width: 480px) {
  .about {
    padding: 15px 14px;
  }

  .intro {
    margin-bottom: 15px;
    padding-bottom: 15px;
  }

  .avatar {
    height: 70px;
    margin-right: 15px;
    width: 70px;
  }

  .name p {
    font-size: 16px;
  }

  .contact {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 80px;
    height: auto;
    justify-content: center;
    padding: 5px;
    width: 40px;
  }

  .contact .image {
    height: 24px;
    width: 24px;
  }

  .description p {
    font-size: 16px;
  }
}

.section-title {
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 28px;
  margin-bottom: 9px;
  padding-bottom: 10px;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
}

.card {
  background-color: #212121;
  border-radius: 15px;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: calc(50% - 30px);
  margin: 15px 0;
  padding: 24px 20px;
}

.card.even {
  margin-right: 15px;
}

.card.odd {
  margin-left: 15px;
}

.card-title {
  font-size: 20px;
}

.card-description {
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 14px;
  margin-top: 8px;
  min-height: 150px;
  opacity: 1;
  padding: 14px 0;
}

@media (max-width: 640px) {
  .projects {
    max-width: 480px;
    margin: 0 auto;
  }

  .section-title {
    font-size: 22px;
  }

  .card {
    flex: 1 0 100%;
    margin: 7px 0;
    padding: 16px 12px;
  }

  .card-description {
    min-height: 80px;
  }

  .card.odd,
  .card.even {
    margin-right: 0;
    margin-left: 0;
  }

  .card h4 {
    font-size: 16px;
  }

  .card p {
    font-size: 14px;
  }

  .card a {
    font-size: 14px;
  }
}
* {
  box-sizing: border-box;
}

body {
  background-color: #171717;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body,
p,
ul,
h4,
h5,
li {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  color: #ff5e5e;
  -webkit-transition: .3s color ease-in-out;
  transition: .3s color ease-in-out;
  text-decoration: none;
}

a:hover {
  color: rgba(255, 94, 94, .68);
}

p {
  opacity: .8;
}

main {
  max-width: 640px;
  margin: 80px auto 50px;
}

@media (max-width: 480px) {
  main {
    padding: 0 10px;
    margin: 40px auto 20px;
  }
}
